.footerContainer {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    background-color: rgb(44, 61, 81);
    color: white;
    padding: 2%;
}

.footerSecondContainer {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    width: 70%;
}

.footerDescription {
    width: 38%;
    min-width: 260px;
    font-size: 11px;
    text-align: end;
    margin-right: 4%;
}
.footerLogo {
    width: 20%;
    min-width: 100px;
    max-height: 130px;
    margin: 0 3%;
}

li {
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
}

@media only screen and (max-width: 1000px) {
    .footerDescription {
        width: 100%;
    }

    .footerSecondContainer {
        flex-direction: column;
    }
}

@media only screen and (max-width: 400px) {
    .footerSecondContainer {
        flex-direction: column;
    }
    .footerLogo {
        min-width: 50px;
        height: 95%;
        margin-top: 20%;
    }
}
